@mixin u-state-layer-opacity {
  --u-state-layer-opacity: 0;

  &:active {
    --u-state-layer-opacity: var(--u-focus-state-layer-opacity);
  }

  @include media-breakpoint-up(lg) {
    &:hover {
      --u-state-layer-opacity: var(--u-hover-state-layer-opacity);
    }

    &:focus {
      --u-state-layer-opacity: var(--u-focus-state-layer-opacity);
    }
  }
}

@mixin u-selectable-background {
  @include u-state-layer-opacity;

  position: relative;

  &::before {
    position: absolute;
    inset: 0;
    content: "";
    background-color: var(--u-state-layer-color);
    border-radius: inherit;
    opacity: var(--u-state-layer-opacity);
    transition: opacity 250ms linear;
  }
}
