@import "variables";
@import "../../animations";
@import "../../colors";
@import "../../grid/variables";
@import "../../zindex";
@import "../../mixins";

:root {
  --u-drawer-sidebar-width: #{$u-drawer-sidebar-width};
}

.u-drawer-backdrop {
  position: absolute;
  inset: 0;
  z-index: $zindex-sidebar - 1;
  pointer-events: none;
  content: "";
  background-color: $u-drawer-backdrop-bg;
  opacity: 0;
  transition: opacity 375ms linear, z-index 0ms 375ms;
}

.u-drawer-content {
  @include u-elevation-tint(1);
  position: fixed;
  top: 0;
  inset-inline-start: -100%;
  bottom: 0;
  z-index: $zindex-sidebar;
  flex-shrink: 0;
  width: var(--u-drawer-sidebar-width);
  max-width: 85%;
  background-color: var(--u-surface-color);
  border-start-end-radius: $u-drawer-border-radius;
  border-end-end-radius: $u-drawer-border-radius;
  overflow-y: hidden;
  transition: var(--u-drawer-transition-property) 375ms $awesome-cubic-bezier;
}

.u-drawer-scrollable-container {
  height: 100%;
  overflow-y: auto;
}

.u-drawer {
  --u-drawer-transition-property: inset-inline-start;

  .u-divider {
    margin-block: $u-drawer-divider-margin;
  }

  &.open {
    .u-drawer-content {
      inset-inline-start: 0;
    }

    > .u-drawer-backdrop {
      pointer-events: auto;
      opacity: $u-drawer-backdrop-opacity;
      transition: opacity 375ms linear;
    }

    &.u-drawer-end {
      .u-drawer-content {
        inset-inline-end: 0;
      }
    }
  }

  &.u-drawer-end {
    --u-drawer-transition-property: inset-inline-end;

    .u-drawer-content {
      inset-inline: auto -100%;
      border-start-end-radius: 0;
      border-end-end-radius: 0;
      border-start-start-radius: $u-drawer-border-radius;
      border-end-start-radius: $u-drawer-border-radius;
    }
  }
}

@each $breakpoint in map-keys($grid-breakpoints) {
  $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

  @include media-breakpoint-up($breakpoint) {
    .u-drawer#{$infix}-permanent,
    .u-drawer#{$infix}-dismissible {
      padding-inline-start: var(--u-drawer-sidebar-width);

      .u-drawer-content {
        inset-inline-start: 0;
        border-radius: 0;
        background-image: none;
      }

      &.u-drawer-end {
        .u-drawer-content {
          inset-inline: auto 0;
        }
      }

      > .u-drawer-backdrop {
        display: none;
      }

      .u-toolbar-fixed {
        inset-inline-start: var(--u-drawer-sidebar-width);
      }

      &.u-drawer-end {
        padding-inline: 0 var(--u-drawer-sidebar-width);

        .u-toolbar-fixed {
          inset-inline: 0 var(--u-drawer-sidebar-width);        }
      }
    }

    .u-drawer#{$infix}-dismissible {
      transition: padding-inline-start 375ms $awesome-cubic-bezier;

      .u-toolbar-fixed {
        transition: var(--u-drawer-transition-property) 375ms $awesome-cubic-bezier, --u-current-elevation-tint-color 200ms;
        will-change: var(--u-drawer-transition-property);
      }

      &.dismiss {
        padding-inline-start: 0;

        .u-drawer-content {
          inset-inline-start: -100%;
        }

        .u-toolbar-fixed {
          inset-inline-start: 0;
        }

        &.u-drawer-end {
          padding-inline-end: 0;

          .u-drawer-content {
            inset-inline-end: -100%;
          }

          .u-toolbar-fixed {
            inset-inline-end: 0;
          }
        }
      }
    }
  }
}
