@import "variables";
@import "../../font";

// stylelint-disable selector-no-qualifying-type

.u-data-table {
  font-family: var(--u-font-family-base);

  table {
    min-width: 100%;
    border-collapse: collapse;
  }

  thead th {
    padding-block: $u-data-table-column-header-vertical-padding;
    line-height: $u-data-table-column-header-line-height;
  }

  td,
  tbody th {
    padding-block: $u-data-table-column-vertical-padding;
  }

  th {
    font-size: $u-data-table-column-header-font-size;
    font-weight: $u-data-table-column-header-font-weight;
    color: var(--u-low-emphasis-color);
    text-align: initial;
  }

  td {
    font-size: $u-data-table-content-font-size;
    font-weight: $u-data-table-content-font-weight;
  }

  tr:not(:first-child),
  thead + tbody {
    th,
    td {
      border-top: $u-data-table-border;
    }
  }

  th,
  td {
    padding-inline: $u-data-table-horizontal-padding;
    vertical-align: top;

    &.no-vertical-padding {
      padding-block: 0;
    }
  }

  &.u-card {
    overflow: visible;
  }
}

.u-data-table-footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: $u-data-table-card-footer-height;
  font-size: $u-data-table-card-footer-font-size;
  font-weight: $u-data-table-card-footer-font-weight;
  color: var(--u-low-emphasis-color);
  border-top: $u-data-table-border;
  padding-inline: $u-data-table-horizontal-padding;

  .u-data-table-actions {
    margin-left: 2rem;
  }
}

.u-data-table-hover tr {
  @include u-state-layer-opacity;

  --u-data-table-hover-color: rgba(var(--u-current-text-color-rgb), var(--u-state-layer-opacity));

  td {
    background-color: var(--u-data-table-hover-color);
  }
}
