@import "../grid/variables";
@import "../mixins";

@for $i from 1 to 25 {
  .u-elevation-shadow#{$i} {
    @include u-elevation-shadow($i);
  }
}

.u-elevation-tint {
  @include u-elevation-tint;
}

@each $elevation, $opacity in $u-surface-tint-opacity {
  .u-elevation-tint#{$elevation} {
    @include u-elevation-tint-important($elevation);
  }
}
