@each $color-name, $color-value in $u-theme-colors {

  .u-text-bg-#{$color-name} {
    @include u-text-bg-variant-important($color-name);
  }

  .u-text-bg-#{$color-name}-container {
    @include u-text-bg-variant-important(#{$color-name}-container);
  }
}

@each $color-name, $color-value in $u-common-colors {

  .u-text-bg-#{$color-name} {
    @include u-text-bg-variant-important($color-name);
  }

  .u-text-bg-#{$color-name}-container {
    @include u-text-bg-variant-important(#{$color-name}-container);
  }
}

.u-text-bg-surface {
  @include u-text-bg-variant-important(surface);
}

.u-text-bg-inverse-surface {
  @include u-text-bg-variant-important(inverse-surface);
}

.u-text-bg-surface-variant {
  @include u-text-bg-variant-important(surface-variant);
}

.u-text-bg-inverse-surface-variant {
  @include u-text-bg-variant-important(inverse-surface-variant);
}

.u-text-bg-body {
  @include u-text-bg-variant-important(background);
}

.u-text-bg-inverse-mode {
  @include u-text-bg-variant-important(inverse-background);
}

.u-text-bg-light-mode {
  @include u-text-bg-variant-important(light-background);
}

.u-text-bg-dark-mode {
  @include u-text-bg-variant-important(dark-background);
}
