:root {
  --u-font-family-base: #{$u-font-family-base};

  --u-state-layer-color: currentColor;
  --u-hover-state-layer-opacity: .08;
  --u-focus-state-layer-opacity: .12;
  --u-pressed-state-layer-opacity: .12;
  --u-dragged-state-layer-opacity: .16;
  --u-disabled-state-background-opacity: .12;
  --u-disabled-state-label-opacity: .57;
  --u-disabled-state-opacity: .38;
  --u-high-emphasis-opacity: 1;
  --u-low-emphasis-opacity: .75;
  --u-lower-emphasis-opacity: .5;
  --u-elevation-tint-color-rgb: var(--u-primary-color-rgb);
  --u-elevation-tint-opacity: 0;

  --u-current-text-color-rgb: var(--u-on-background-color-rgb);
  --u-high-emphasis-color: rgba(var(--u-current-text-color-rgb), var(--u-high-emphasis-opacity));
  --u-low-emphasis-color: rgba(var(--u-current-text-color-rgb), var(--u-low-emphasis-opacity));
  --u-lower-emphasis-color: rgba(var(--u-current-text-color-rgb), var(--u-lower-emphasis-opacity));

  @each $color-name, $color-value in $u-common-colors {
    @include common-colors-vars($color-value, $color-name);
  }

  @each $name, $value in $u-font-weights {
    --u-font-weight-#{$name}: #{$value};
  }

  @include _elevation-tint-opacity-vars;

  @if $enable-grid-classes {
    --u-container-width: #{$container-width};
    --u-container-margin: #{$margin-gutter-default};
    --u-container-gutter: #{$margin-gutter-default};
  }
}

.u-dark-mode {
  --u-current-text-color-rgb: var(--u-on-background-color-rgb);
  --u-high-emphasis-color: rgba(var(--u-on-background-color-rgb), var(--u-high-emphasis-opacity));
  --u-low-emphasis-color: rgba(var(--u-on-background-color-rgb), var(--u-low-emphasis-opacity));
  --u-lower-emphasis-color: rgba(var(--u-on-background-color-rgb), var(--u-lower-emphasis-opacity));
}
