@mixin u-elevation-tint-placeholders {
  @property --u-current-elevation-tint-color {
    syntax: "<color>";
    initial-value: transparent;
    inherits: false;
  }

  @each $elevation, $opacity in $u-surface-tint-opacity {
    @property --u-elevation-tint#{$elevation}-color {
      syntax: "<color>";
      initial-value: rgba(var(--u-elevation-tint-color-rgb), var(--u-elevation-tint#{$elevation}-opacity));
      inherits: false;
    }
  }
}
