@import "variables";
@import "../../font";
@import "../../mixins";
@import "../../theme";

.u-floating-action-area {
  position: fixed;
  inset-inline-end: $u-floating-action-area-margin;
  bottom: $u-floating-action-area-margin;
}

.u-floating-menu {
  display: inline-flex;
  align-items: flex-end;
  flex-direction: column;

  .u-floating-btn + .u-floating-btn {
    margin-top: $u-floating-action-area-margin;
  }
}
